import React from 'react';
import {withRouter} from 'react-router-dom';

export default withRouter(function ScrollToTop({history}) {
    
    React.useEffect(() => {
        const unlisten = history.listen(() => {
            window.scrollTo(0, 0);
        });

        return () => {
            unlisten();
        };
    }, [history]);

    return (null);
});