import React from 'react';
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography';
import Header from '../PartialViews/section-header';
import Skills from '../PartialViews/skills';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles(() => ({
    button: {
        borderRadius: "30px",
        padding: "7px 50px",
        color: "#f5deb3",
        border: "2px solid #f5deb3",
        backgroundImage: "linear-gradient(to top, rgba(245, 222, 179, 0.3), rgba(76, 142, 203, 0.6))"
    }
}))

const handleClick = (event) => {
    const anchor = (event.target.ownerDocument || document).querySelector('#scroll-to-top-anchor');

    if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
};

export default function About() {
    const classes = useStyles();
    return (
        <>
            <Header>
                <Typography variant="h2" style={{ color: "wheat" }} className={`${"wow fadeInRight"}`} gutterBottom><b>Hello</b></Typography>
                <Typography variant="body2" style={{color:"white", animatinoDelay:"0.2s", MozAnimationDelay: "0.2s", WebkitAnimationDelay: "0.2s"}} className={`${"wow fadeInRight"}`}>
                    <Box textAlign="justify" component="span">
                        I'm an enthusiast in Web design and development. I have worked in an IT role, taking on responsibilities and assisting in the design and development of several projects using the skills I've obtained over a period of 5 years.
                        I have a Bachelors Degree in Chemical Engineering from the University of Ilorin and currently pursuing a Masters Degree in Process Engineering.
                        I have found a way to combine my knowledge in Chemical Engineering with my programming skills.
                    </Box>
                </Typography>
                <Box mt={3}>
                    <Button 
                        variant="outlined" 
                        onClick={handleClick}
                        className={`${classes.button} ${"wow"} ${"rubberBand"}`} 
                        style={{animatinoDelay:"0.5s", MozAnimationDelay: "0.5s", WebkitAnimationDelay: "0.5s"}}
                        disableElevation
                        >Learn more</Button>
                </Box>
            </Header>
            <Skills />
        </>
    )
}