import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';

import blend from '../../static/images/blender-logo.png';
import doughnut from '../../static/images/3d/doughnut4.jpg';
import oc from '../../static/images/3d/oc_wallpaper.jpg';
import ring from '../../static/images/3d/phonewallpaper.jpg';
import pModel from '../../static/images/3d/sample11.jpg';
import bModel from '../../static/images/3d/sample14.jpg';
import city from '../../static/images/3d/wallpaper2.jpg';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        backgroundColor: theme.palette.background.paper,
    },
    gridList: {
        width: 500,
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    },
    titleBar: {
        background:
            'linear-gradient(to top, rgba(0,0,0,0.4) 0%, ' +
            'rgba(0,0,0,0.1) 70%, rgba(0,0,0,0) 100%)',
    },
    icon: {
        color: 'white',
    },
}));

const tileData = [
    {
        img: pModel,
        title: '3D Model',
        author: 'author',
        featured: true,
    },
    {
        img: oc,
        title: 'OC',
        author: 'author',
    },
    {
        img: ring,
        title: 'Ring',
        author: 'author',
    },
    {
        img: city,
        title: 'City',
        author: 'author',
    },
    {
        img: doughnut,
        title: 'Doughnut',
        author: 'author',
    },
    {
        img: bModel,
        title: '3D Character',
        author: 'author',
        featured: true,
    },
];

export default function ThreeD() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <GridList cellHeight={200} spacing={3} className={classes.gridList} col={3}>
                {tileData.map((tile) => (
                    <GridListTile key={tile.img} cols={tile.featured ? 2 : 1} rows={tile.featured ? 2 : 1}>
                        <img src={tile.img} alt={tile.title} />
                            <GridListTileBar
                                titlePosition="bottom"
                                actionIcon={
                                    <>
                                        <IconButton>
                                            <img src={blend} width="25px" alt="Blender" />
                                        </IconButton>
                                    </>
                                }
                                actionPosition="left"
                                className={classes.titleBar}
                            />
                    </GridListTile>

                ))}
            </GridList>
        </div>
    );
}